import React from 'react'
import css from './SliderBanner.module.scss'
import BannerTemplate from 'components/BannerTemplate/BannerTemplate'
import Slider from 'components/Slider/Slider'
import team1 from 'assets/images/slider__item--1.png'
import team2 from 'assets/images/slider__item--2.png'
import team3 from 'assets/images/slider__item--3.png'
import team4 from 'assets/images/slider__item--4.png'
import { useDispatch } from 'react-redux'
import { setVisibleBanner } from 'store/actions'

const SliderBanner = ({ id }) => {
  const data = [
    {
      photo: team1,
      contacts: {
        name: 'Эрик Найман',
        rank: 'Глава компании WMHUG\'S',
      },
      description: {
        caption: 'Эрик Найман — руководитель Wealth Management.',
        list: [
          `Управляющий партнер HUG\'S, один из&nbsp;самых успешных профессионалов по&nbsp;управлению активами с&nbsp;1995&nbsp;г.`,
          `Автор 5 книг по инвестированию на мировых финансовых рынках.`,
          `Создал и руководил инвестиционно-банковским подразделением "Укрсоцбанка" (2001-2011).`,
        ]
      }
    },
    {
      photo: team2,
      contacts: {
        name: 'Дмитрий Жипецкий',
        rank: 'Партнер',
      },
      description: {
        list: [
          `Разработка и&nbsp;проведение обучающих семинаров для сотрудников Private Banking ряда украинских банков.`,
          `Ведущий оператор опционного рынка на&nbsp;Украинской бирже в&nbsp;2012&nbsp;году.`
        ]
      }
    },
    {
      photo: team3,
      contacts: {
        name: 'Валентина Скиба',
        rank: 'Руководитель отдела продаж',
      },
      description: {
        list: [
          `Опыт работы в&nbsp;банковском секторе более 15&nbsp;лет. Из них более 10&nbsp;лет возглавляла подразделение Private Banking, как в&nbsp;украинских банках, так и&nbsp;в&nbsp;банках с&nbsp;иностранным капиталом.`,
        ]
      }
    },
    {
      photo: team4,
      contacts: {
        name: 'Артем Щербина',
        rank: 'Главный аналитик',
      },
      description: {
        list: [
          `10-летний опыт личного инвестирования на&nbsp;фондовом рынке Украины и&nbsp;США.`,
          `Кандитат экономических наук, более 30&nbsp;научных работ в&nbsp;области инвестирования.`,
          `Автор инвестиционной стратегии с&nbsp;исторической доходностью выше&nbsp;30% годовых. Реализовано инвестиционных идей на&nbsp;сумму более 80&nbsp;млн. долларов.`,
        ]
      }
    },
  ]

  const dispatch = useDispatch()

  return (
    <>
      <BannerTemplate
        bgColorClass={css.wrapper}
        heading='Наша команда'
        slideIndex={4}
        outOfContainerElements={(
          <Slider
            data={data}
          />
        )}
        id={id}
        onLeave={evt => {
          if (evt.currentPosition === 'above') {
            dispatch(setVisibleBanner(null))
          }
        }}
      >
        <div className={css.content}>
          <div className={css.intro}>
            <p className={css.slogan}>
              Лучшие в мире инвестиций
            </p>
            <p className={css.descriptor}>
              Ваши активы в&nbsp;надежных руках. У&nbsp;нас сильная опытная команда настоящих профессионалов во&nbsp;главе с&nbsp;Эриком Найманом
            </p>
          </div>
        </div>
      </BannerTemplate>
    </>
  )
}

export default SliderBanner
